/**
 * Popgen .btn Styles
 * 
 */
.btn-flip .btn {
  position: relative;
  border: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  color: rgba(0, 0, 0, 0);
  background: transparent;
  border-radius: .25rem;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
}
.btn-flip .btn,
.btn-flip .btn:after,
.btn-flip .btn:before {
  padding: .6875rem 2rem;
  -webkit-transition: -webkit-transform 0.75s, background-color 0.125s;
  -moz-transition: -moz-transform 0.75s, background-color 0.125s;
  -ms-transition: -ms-transform 0.75s, background-color 0.125s;
  transition: transform 0.75s, background-color 0.125s;
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -ms-transform-style: preserve-3d;
  transform-style: preserve-3d;
}
.btn-flip .btn:after,
.btn-flip .btn:before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: .25rem;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  backface-visibility: hidden;
}
.btn-flip .btn:before {
  z-index: 2;
  color: #fff;
  background-color: inherit;
  content: attr(data-label);
}
.btn-flip .btn:after {
  z-index: 1;
  background-color: #999;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(data:image/gif;base64,);
  transform: rotateX(180deg);
  content: '';
}
.btn-flip .btn:active:before {
  background-color: darkent(inherit, 10%);
}
.btn-flip .btn.loading {
  -webkit-transform: rotateX(180deg);
  -moz-transform: rotateX(180deg);
  -ms-transform: rotateX(180deg);
  transform: rotateX(180deg);
}
