@media (min-width: 768px) {
  #first-time-faq .modal-content .icon a {
    font-size: 135px;
  }
}
@media (min-width: 992px) {
  #first-time-faq .modal-content .icon a {
    font-size: 142.5px;
  }
}
@media (min-width: 1200px) {
  #first-time-faq .modal-content .icon a {
