  }
}
#first-time-faq .modal-content .icon a:active .fa {
  animation: color-change 1s infinite;
}
#first-time-faq .modal-content .icon a:hover {
  color: #f1c40f;
}
#first-time-faq .modal-content .icon a:visited .fa {
  animation: color-change 1s infinite;
}
#first-time-faq .modal-content .icon a:focus .fa {
  animation: color-change 1s infinite;
}
@keyframes color-change {
  0% {
    color: #f1c40f;
  }
  50% {
    color: #f7dc6f;
  }
  100% {
    color: #f1c40f;
