  #first-time-faq .modal-content h2 {
    font-size: 38px;
  }
}
#first-time-faq .modal-content h3 a {
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: #4b516a;
}
#first-time-faq .modal-content h3 a:hover {
  text-decoration: none;
}
#first-time-faq .modal-content h3 a::after {
  position: absolute;
  top: 90%;
  left: 0;
  width: 100%;
  height: 1px;
  background: #4b516a;
  content: '';
  opacity: 0;
  transition: height 0.3s, opacity 0.3s, transform 0.3s;
  transform: translateY(-10px);
}
#first-time-faq .modal-content h3 a:hover::after,
#first-time-faq .modal-content h3 a:focus::after {
  height: 3px;
  opacity: 1;
  transform: translateY(0px);
}
#first-time-faq .modal-footer .btn {
  background: none;
  color: inherit;
  vertical-align: middle;
  position: relative;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  background: #4b516a;
  color: #fff;
  transition: background-color 0.3s, color 0.3s;
}
#first-time-faq .modal-footer .btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid #4b516a;
  z-index: -1;
  border-radius: inherit;
  opacity: 0;
  transform: scale3d(0.6, 0.6, 1);
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
#first-time-faq .modal-footer .btn:hover {
  background-color: #fff;
  color: #4b516a;
}
#first-time-faq .modal-footer .btn:hover::before {
  transform: scale3d(1, 1, 1);
  opacity: 1;
}
