.progress-bar-mat {
  position: relative;
  height: 4px;
  display: block;
  width: 100%;
  background-color: #b6bacb;
  border-radius: 2px;
  margin-top: 0;
  margin-bottom: 0;
  overflow: hidden;
}
.progress-bar-mat .determinate {
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: #4b516a;
  transition-delay: 0s;
  transition-duration: 0.25s;
  transition-property: width;
  transition-timing-function: ease;
}
.progress-bar-mat .indeterminate {
  background-color: #4b516a;
}
.progress-bar-mat .indeterminate:before {
  content: '';
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  will-change: left, right;
  animation: indeterminate 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;
}
.progress-bar-mat .indeterminate:after {
  content: '';
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  will-change: left, right;
  animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
  animation-delay: 1.15s;
}
@keyframes indeterminate {
  0% {
    left: -35%;
    right: 100%;
  }
  60% {
    left: 100%;
    right: -90%;
  }
  100% {
    left: 100%;
    right: -90%;
  }
}
@keyframes indeterminate-short {
  0% {
    left: -200%;
    right: 100%;
  }
  60% {
    left: 107%;
    right: -8%;
  }
  100% {
    left: 107%;
    right: -8%;
  }
}
